*, ::after, ::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.left {
  padding: 1em;
  text-align: left;
  margin: 0;
  width: 200px;
}

.right {
  padding: 1em;
  flex-grow: 1;
}

.parent {
  display: flex;
  overflow: hidden;
}

.navigation ul {
  margin-bottom: 40px;
}

.navigation > ul {
  padding-left: 0;
  list-style: none;
  margin-top: 0;
}

.navigation > h2 {
  margin-top: 0;
}

.navigation > ul > li a {
  text-decoration: none;
  border-bottom: 2px solid #000;
  color: #000;
  display: inline-block;
  width: 190px;
  padding: 10px;
}

.navigation > ul > li a:hover { background: #ccc; }

.navigation > ul { margin-top: 0; }

.componentpage { padding-left: 20px; }

.componentpage > h2 { margin-top: 0; }

.hljs { padding: 1.5em !important; }

table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1rem;
  border-collapse: collapse;
}

table > thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
}

table > thead > tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}

table > th {
  padding: .75rem;
  vertical-align: top;
  border: 1px solid #dee2e6;
}

th { text-align: left }

td,
th
{
  padding: .75rem;
  vertical-align: top;
  border: 1px solid #dee2e6;
}

pre {
  overflow: auto;
  box-sizing: border-box;
}

button {
  padding: 10px 15px;
  font-size: 15px;
  border-radius: 4px;
  border: none;
  color: #000;
}

button:active {
  color: #000;
  background: rgba(0, 0, 0, .4);
}

.codeexample {
  margin: 30px 0 60px 0;
}

header {
  overflow: auto;
  padding: 0 15px;
}

/* Panel styles */

.panel {
  border-radius: 4px;
  background: rgba(225, 197, 167, 0.15);
  max-width: 33.33%;
  padding: 20px;
  overflow: auto;
  margin-bottom: 30px;
}

.panel > div {
  margin: 10px -20px 0;
  padding: 0 20px;
  padding-top: 10px;
}

.panel ul {
  list-style: none;
}

.panel header > h2 {
  margin: 0;
  color: rgb(51, 51, 51);
}

.panel header,
.panel ul {
  padding-left: 0;
}

.panel ul {
  margin: 10px 0;
}

.panel li {
  padding: 15px 20px 15px 0;
  border-bottom: 1px solid #ccc;
}

.panel header {
  margin: -20px;
  background: #ccc;
  padding: 15px 20px;
}

.panel header.success {
  background: rgb(167, 225, 180);
}

.panel header.warning {
  background: rgb(252, 202, 170);
}


/* Button styles */

.btn {
  border-radius: 0;
  margin: 0;
  border: none;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: .5px;
}

.btn:hover {
  background: rgba(0, 0, 0, .35);
  cursor: pointer;
}

.btn:active {
  background: rgba(0, 0, 0, .5);
  color: #000;
  cursor: pointer;
}

.btn.default {
  background: rgb(174, 174, 174);
}

.btn.default:hover {
  background: rgb(148, 148, 148);
}

.btn.info {
  background: rgb(105, 172, 225);
}

.btn.info:hover {
  background: rgb(96, 157, 206);
}

.btn.small {
  padding: 5px 20px;
  height: 40px;
}

.btn.medium {
  padding: 10px 40px;
  height: 50px;
  font-size: 16px;
}

.btn.large {
  padding: 20px 60px;
  height: 60px;
  font-size: 18px;
}
